import NavbarSection, {SectionMouseActions, SectionProps} from "./navbar-section";
import NavbarTitle from "./navbar-title";
import NavbarGraphic from "./navbar-graphic";
import SocialIcons from "./social-icons";
import React from "react";
import {NavbarState, Section} from "./app";

export type NavbarProps = {
  navBarState: NavbarState;
  setNavBarState: (n: NavbarState) => void;
  offsetsMap: Map<Section, number>;
}

export function getSectionProps(navbarState: NavbarState,
                                setNavBarState: (n: NavbarState) => void,
                                section: Section,
                                className: string
): SectionProps {
  return {
    ...getSectionMouseActions(navbarState, setNavBarState, section),
    sectionClassName: className,
    isActive: navbarState.activeSection === section,
    isHover: navbarState && navbarState.hoverState === section,
    onSetActive: () => {
      if (navbarState.scrollingState == null || navbarState.scrollingState.to === section) {
        const sectionName = Section[section];
        const url = document.location.pathname + "#" + sectionName.toLowerCase();
        window.history.pushState(null, "", url);
        document.title = `Alexander Chang | ${sectionName}`;
      }
      setNavBarState({
        ...navbarState,
        activeSection: section,
        scrollingState: navbarState?.scrollingState?.to === section ? null : navbarState.scrollingState
      });
    }
  }
}

export function getSectionMouseActions(navbarState: NavbarState,
                                       setNavBarState: (n: NavbarState) => void,
                                       section: Section): SectionMouseActions {
  return {
    section: section,
    onMouseEnter: () =>
      setNavBarState({
        ...navbarState,
        hoverState: section,
      })
    ,
    onMouseLeave: () =>
      setNavBarState({
        ...navbarState,
        hoverState: navbarState.hoverState === section ? null : navbarState.hoverState,
      }),
    onClick: () => {
      setNavBarState({
        ...navbarState,
        scrollingState: {to: section, from: navbarState.activeSection},
      });
    },
  }
}


export function getMouseActionsMap(navBarState: NavbarState, setNavBarState: (n: NavbarState) => void): Map<Section, SectionMouseActions> {
  const sections: Section[] = [Section.About, Section.Publications, Section.Work, Section.Projects, Section.Contact];

  return new Map(sections.map(x => {
    let mouseActions = getSectionMouseActions(navBarState, setNavBarState, x);
    return [x, mouseActions];
  }))
}

export const DesktopNavbar = ({navBarState, setNavBarState, offsetsMap}: NavbarProps) => {
  const sectionClassName = "navbar-section-container"
  return (
    <div className="navbar">
      <NavbarTitle/>
      <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.About, sectionClassName)}/>
      <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Publications, sectionClassName)}/>
      <div className="navbar-graphic-container">
        <NavbarGraphic mouseActionsMap={getMouseActionsMap(navBarState, setNavBarState)}
                       offsetsMap={offsetsMap}
                       activeSection={navBarState.activeSection}
                       hoverState={navBarState.hoverState}
        />
      </div>
      <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Work, sectionClassName)}/>
      <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Projects, sectionClassName)}/>
      <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Contact, sectionClassName)}/>
      <SocialIcons className={"social-icons"}/>
    </div>
  )
}
