import React from "react";
import autogami from '../images/autogami.png';
import genYojson from '../images/gen-yojson.png';
import petr4Project from '../images/petr4-project.png';
import {ContentProps} from "./about";

const Projects = React.forwardRef<HTMLInputElement, ContentProps>(({flexColClassName}, ref) => {
  return (
    <div className={flexColClassName} id="projects-content" ref={ref}>
      <div className="poppins-normal-black-24px">Projects</div>
      <img src={autogami} height={"254"} width={"711px"} alt="autogami" className={"project-img first-project-img"}/>
      <div className="projects-title work-heading">Autogami</div>
      <div className="projects-text">
          An origami crease pattern shows the unfolded version of a model. Oftentimes origami crease patterns are
          presented as images on webpages. This presents many issues for origami artists looking to fold these designs.
          The images may scale poorly if they have poor resolution, or are in a lossy format like JPEG. Without
          physically printing the design, it also can be difficult to tell the proportion between different lines.
          Finally, physically creasing a pattern can be cumbersome as complex models can have a high number of creases.
          <br/>
          <br/>
          Autogami was originally developed as a hardware hack project at Cornell Make-a-thon 2019. Our team modified a
          3D printer to act as an paper creaser, and developed a computer vision algorithm for identifying crease
          locations automatically. The output of the algorithm was then transformed into G-Code for creasing.
          <br/>
          <br/>
          After the hackathon, I continued to improve the algorithm and created a standalone webapp for it. After lines
          are
          identified automatically, creases can edited, added, and deleted before exporting. Additional features include
          snapping
          vertices to grid, and mountain and valley folds.
          <br/><a href={"https://github.com/alexchang8/autogami-frontend"}>code</a>
      </div>
      <img src={petr4Project} height={"254px"} width={"711px"} alt="petr4 project" className="project-img-margin"/>
      <div className="projects-title work-heading">Petr4</div>
      <div className="projects-text">
          The Petr4 project developed formal semantics of the P4 Language, with a reference interpreter implementation
          in OCaml.
          Using Js_of_ocaml, the OCaml implementation was compiled to JavaScript allowing the interpreter to run in
          browser. The interpreter and web application were included as artifacts in a paper published to POPL'21.
          <br/><a href={"https://github.com/verified-network-toolchain/petr4/"}>code</a>
      </div>
      <img src={genYojson} height={"254px"} width={"711px"} alt="gen yojson" className="project-img-margin"/>
      <div className="projects-title work-heading">Yojson Type Generator</div>
      <div className="projects-text">
          Gen_yojson is a small utility for converting JSON inputs into OCaml types. This project was inspired by
          similar
          utilities for other languages. The project runs in browser using Js_of_ocaml. Part of the project involved
          forking
          OcamlFormat, so that OcamlFormat could also be compiled to JavaScript for clean output.
          <br/><a href={"https://alexchang8.github.io/gen_yojson/"}>web app</a>&nbsp;&nbsp;&nbsp;
          <a href={"https://github.com/alexchang8/gen_yojson"}>code</a>
      </div>
    </div>
  )
})

export default Projects;