import useViewportSize from "./viewport-width";
import React from "react";

const NavbarTitle = () => {
  const {width} = useViewportSize();
  const breakpoint = 1030;

  const title = width < breakpoint ?
    <div className="name poppins-normal-black-24px">Alexander<br/>Piatko<br/>Chang
    </div>
    :
    <div className="name poppins-normal-black-24px">Alexander Piatko Chang<br/>software
      engineer @ stripe
    </div>
  return (
    <div className={"navbar-title-container"}>
      {title}
    </div>
  )
}

export default NavbarTitle