import React, {CSSProperties, useEffect, useState} from 'react';
import NavbarSection, {SectionMouseActions, SectionProps} from "./navbar-section";
import SocialIcons from "./social-icons";
import NavbarGraphic from "./navbar-graphic";
import {scroller} from "react-scroll";
import About from "./about";
import Publications from "./publications";
import Work from "./work";
import Projects from "./projects";
import Contact from "./contact";
import NavbarTitle from "./navbar-title";
import NavbarButton from "./navbar-button";
import {DesktopNavbar} from "./desktop-navbar";
import useViewportSize, {ViewportSize} from "./viewport-width";
import './stylesheet.css';
import {MobileNavbar} from "./mobile-navbar";

export enum Section {About, Publications, Work, Projects, Contact}

export type HoverState = Section | null

export type ScrollingState = { from: Section, to: Section } | null

export type NavbarState = {
  // section that is visible to the user
  activeSection: Section;
  // if user is hovering over a section link in navbar
  hoverState: HoverState;
  // if user has clicked a section link and in process of scrolling
  // TODO: deprecate this
  scrollingState: ScrollingState;
  topOffsetsMap: Map<Section, number>;
  isMobile: boolean;
}


function sectionOfString(s: String): Section {
  switch (s) {
    case "about":
      return Section.About;
    case "publications":
      return Section.Publications;
    case "work":
      return Section.Work;
    case "projects":
      return Section.Projects;
    case "contact":
      return Section.Contact;
    default:
      //TODO: 404 section?
      return Section.About
  }
}

export function isMobile({width, height}: ViewportSize): boolean {
  const widthBreakpoint = 950;
  const heightBreakpoint = 630;
  return width < widthBreakpoint || height < heightBreakpoint;
}

function App() {
  const getActiveSection = () => sectionOfString(window.location.hash.substring(1));
  const initialState: NavbarState = {
    activeSection: getActiveSection(),
    hoverState: null,
    scrollingState: null,
    topOffsetsMap: new Map(),
    isMobile: false
  }
  const [navBarState, setNavBarState] = useState<NavbarState>(initialState)

  function scrollToSection(section: Section) {
    setNavBarState({...navBarState, activeSection: section})
    let sectionElement = Section[section].toLowerCase() + "-content";
    //TODO: restore exact scroll position
    scroller.scrollTo(sectionElement, {
      smooth: false,
      offset: -73
    })
  }

  const onHashChange = () => {
    //update rendered section when user changes hash manually
    scrollToSection(getActiveSection())
  }

  useEffect(() => {
      // scroll to initial section
      // TODO: update to be exact scrolling?
      scrollToSection(initialState.activeSection)
      window.addEventListener('hashchange', onHashChange);
      return () => window.removeEventListener('hashchange', onHashChange);
    },
    //only mount once
    [])

  const viewportSize = useViewportSize();
  const mobile = isMobile(viewportSize);
  const aboutRef = React.useRef<HTMLInputElement>(null);
  const publicationsRef = React.useRef<HTMLInputElement>(null);
  const workRef = React.useRef<HTMLInputElement>(null);
  const projectsRef = React.useRef<HTMLInputElement>(null);
  const contactRef = React.useRef<HTMLInputElement>(null);

  //TODO: this polls whenever entering a new section, but will not resize until active section changes.
  const offsetsMap: Map<Section, number> = new Map();
  const scrollTop = document.documentElement.scrollTop;
  if (aboutRef.current != null) {
    offsetsMap.set(Section.About, aboutRef.current.getBoundingClientRect().top + scrollTop)
  }
  if (publicationsRef.current != null) {
    //TODO: move into constant
    offsetsMap.set(Section.Publications, publicationsRef.current.getBoundingClientRect().top + scrollTop - 73)
  }
  if (workRef.current != null) {
    offsetsMap.set(Section.Work, workRef.current.getBoundingClientRect().top + scrollTop - 73)
  }
  if (projectsRef.current != null) {
    offsetsMap.set(Section.Projects, projectsRef.current.getBoundingClientRect().top + scrollTop - 73)
  }
  if (contactRef.current != null) {
    offsetsMap.set(Section.Contact, contactRef.current.getBoundingClientRect().top + scrollTop - 73)
  }

  const flexColClassName = mobile ? "mobile-flex-col" : "flex-col";
  const firstFlexCol = mobile ? "first-mobile-flex-col " + flexColClassName : flexColClassName;
  return (
    <>
      {
        mobile ?
          <MobileNavbar
            navBarState={navBarState}
            setNavBarState={setNavBarState}
            offsetsMap={offsetsMap}/> :
          <DesktopNavbar navBarState={navBarState}
                         setNavBarState={setNavBarState}
                         offsetsMap={offsetsMap}/>
      }

      <div className="flex-content-container">
        {mobile ? <></> :
          <>
            <div className="navbar-space"/>
          </>
        }
        <div className="content-container">
          <About ref={aboutRef} flexColClassName={firstFlexCol}/>
          <Publications ref={publicationsRef} flexColClassName={flexColClassName}/>
          <Work ref={workRef} flexColClassName={flexColClassName}/>
          <Projects ref={projectsRef} flexColClassName={flexColClassName}/>
          <Contact ref={contactRef} flexColClassName={flexColClassName}/>
        </div>
      </div>
    </>
  );
}


export default App