import React from 'react';
import navbarHorizontalLine from '../images/line-6.svg';
import redNavbarHorizontalLine from '../images/line-10.svg';
import hoverNavbarHorizontalLine from '../images/line-hover-navbar-section.svg';
import './stylesheet.css';
import {Link} from 'react-scroll';
import {Section} from "./app";

export type SectionMouseActions = {
  section: Section
  onMouseEnter: () => void;
  onMouseLeave: () => void;
  onClick: () => void;
}


export type SectionProps = SectionMouseActions & {
  isActive: boolean;
  isHover: boolean;
  // called when section is visible in viewport
  onSetActive: () => void;
  sectionClassName: string;
}

function getClassName(isActive: boolean, isHover: boolean): string {
  if (isActive) {
    return "red navbar-header"
  } else if (isHover) {
    return "navbar-header hover-navbar-header"
  } else {
    return "navbar-header"
  }
}

const NavbarSection = ({
                         section,
                         isActive,
                         isHover,
                         onMouseEnter,
                         onMouseLeave,
                         onSetActive,
                         onClick,
                         sectionClassName
                       }: SectionProps) => {
  const className = getClassName(isActive, isHover);
  // TODO: change this to be a CSS filter
  const navbarLineSrc = isActive ? redNavbarHorizontalLine : isHover ? hoverNavbarHorizontalLine : navbarHorizontalLine;
  let sectionName = Section[section]
  let sectionLower = sectionName.toLowerCase();
  return (
    <Link to={sectionLower + "-content"}
          spy={true}
          smooth={true}
          duration={500}
          onSetActive={onSetActive}
          offset={-73}
          onClick={onClick}
          className={sectionClassName}
    >
      <div className={className + " " + "work"} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
        {sectionName.toUpperCase()}
      </div>
      <div onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}
           className={sectionLower + "-navbar-section"}>
        <img src={navbarLineSrc} alt="line"
             className={"navbar-section-line"}
        />
      </div>
    </Link>
  );
}

export default NavbarSection;