import {useState} from "react";

export type ButtonProps = {
  toggleState: () => void;
  dropdownOpen: boolean;
}


const NavbarButton = ({toggleState, dropdownOpen}: ButtonProps) => {
  const [isHover, setIsHover] = useState<boolean>(false)
  const color = isHover ? "var(--hover-navbar-color)" : "black";

  return (<button onMouseEnter={() => setIsHover(true)} onMouseLeave={() => setIsHover(false)}
                  onClick={toggleState}
                  className={"navbar-toggle-button"}
  >
    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
      {
        dropdownOpen ?
          <path fillRule="evenodd" clipRule="evenodd"
                d="M19 6.41L17.59 5L12 10.59L6.41 5L5 6.41L10.59 12L5 17.59L6.41 19L12 13.41L17.59 19L19 17.59L13.41 12L19 6.41Z"
                fill={color} fillOpacity="0.87"/> :
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fillRule="evenodd" clipRule="evenodd" d="M3 18H21V16H3V18ZM3 13H21V11H3V13ZM3 6V8H21V6H3Z"
                  fill={color} fillOpacity="0.87"/>
          </svg>
      }
    </svg>
  </button>)
}

export default NavbarButton