const SvgLink = ({path, mouseActions}: SvgLinkProps) => {
  return (
    <Link to={Section[mouseActions.section].toLowerCase() + "-content"} smooth={true} duration={500}
          offset={-73} {...mouseActions}>
      <path
        d={path}
        fill="#0009DD" fillOpacity="0"
        style={{cursor: "pointer", pointerEvents: "auto"}}
      />
    </Link>)
}

type SvgLinkProps = {
  path: string;
  mouseActions: SectionMouseActions;
}

