import React, {CSSProperties, useState} from "react";
import './stylesheet.css';
import NavbarButton from "./navbar-button";
import NavbarGraphic from "./navbar-graphic";
import {getMouseActionsMap, getSectionProps, NavbarProps} from "./desktop-navbar";
import NavbarSection from "./navbar-section";
import {Section} from "./app";
import SocialIcons from "./social-icons";

function cssVisibility(hide: boolean): CSSProperties {
  return hide ? {visibility: "hidden"} : {};
}

export const MobileNavbar = ({navBarState, setNavBarState, offsetsMap}: NavbarProps) => {
  const sectionClassName = "mobile-navbar-section-container"
  const graphic =
    <NavbarGraphic mouseActionsMap={getMouseActionsMap(navBarState, setNavBarState)}
                   offsetsMap={offsetsMap}
                   activeSection={navBarState.activeSection}
                   hoverState={navBarState.hoverState}
    />
  const [dropdownOpen, setDropdownOpen] = useState<boolean>(false);
  const dropdownStyle: CSSProperties = cssVisibility(!dropdownOpen)
  return (
    <>
      <div className="mobile-navbar">
        {/*TODO*/}
        <NavbarButton toggleState={() => setDropdownOpen(!dropdownOpen)} dropdownOpen={dropdownOpen}/>
        <div className="mobile-navbar-graphic-container-tiny">
          {dropdownOpen ? <></> : graphic}
        </div>
        <div className="mobile-name">
          Alexander<br/>Piatko<br/>Chang
        </div>
      </div>
      <div className="mobile-dropdown" style={dropdownStyle}>
        <div style={{height: "3.5%"}}/>
        <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.About, sectionClassName)}/>
        <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Publications, sectionClassName)}/>
        <div className="navbar-mobile-graphic-container">
          {dropdownOpen ? graphic : <></>}
        </div>
        <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Work, sectionClassName)}/>
        <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Projects, sectionClassName)}/>
        <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Contact, sectionClassName)}/>
        <SocialIcons className={"mobile-social-icons"}/>
      </div>
      <div className={"mobile-dropdown-shader"} style={dropdownStyle}/>
    </>
  )
}