import React, {useState} from 'react';
import github from '../images/github-original-1.svg';
import githubHover from '../images/github-original-1-hover.svg';
import linkedin from '../images/linkedin-original-1.svg';
import linkedinHover from '../images/linkedin-original-1-hover.svg';

type Props = {
  className: string
}

type SingleIconProps = {
  imgSrcNoHover: string
  imgSrcHover: string
  className: string
  href: string
  alt: string
}


const SingleSocialIcon = ({imgSrcNoHover, imgSrcHover, className, href, alt}: SingleIconProps) => {
  const [isHover, setIsHover] = useState<boolean>(false)
  return (
    <a href={href}>
      <img src={isHover ? imgSrcHover : imgSrcNoHover} alt={alt} className={className}
           onMouseEnter={() => setIsHover(true)} onMouseLeave={() => setIsHover(false)}/>
    </a>
  );
}

const SocialIcons = ({className} : Props) => {
  return (
    <div className={className}>
      <SingleSocialIcon imgSrcNoHover={linkedin}
                        imgSrcHover={linkedinHover}
                        className="linked-in-original"
                        href="https://www.linkedin.com/in/apcha/"
                        alt="linkedin"/>
      <SingleSocialIcon imgSrcNoHover={github}
                        imgSrcHover={githubHover}
                        className="github-original"
                        href="https://github.com/alexchang8"
                        alt="github"/>
    </div>
  );
}

export default SocialIcons;