import React from "react";
import cornellLogo from '../images/cornell-logo.svg';
import cuairFull from '../images/cuair-full.svg';
import ngLogoFull from '../images/northrop-full.svg';
import petr4Logo from '../images/petr4-logo-1.svg';
import stripeFull from '../images/stripe-full.svg';
import {ContentProps} from "./about";
import useViewportSize from "./viewport-width";
import {isMobile} from "./app";

type CompanySectionProps = {
  logo: string;
  companyName: string;
  roleText: JSX.Element;
  descriptionText: JSX.Element;
  isFirst: boolean;
  isSquareIcon: boolean;
  isVerticalLayout: boolean;
}

const CompanySection = ({
                          logo,
                          companyName,
                          roleText,
                          descriptionText,
                          isFirst,
                          isSquareIcon,
                          isVerticalLayout
                        }: CompanySectionProps) => {
  const vertical = isVerticalLayout ? "vertical-" : "";
  const companySection = vertical + "company-section";
  let logoClassName = "company-logo-image";
  if (isVerticalLayout) {
    logoClassName = "vertical-company-logo";
  } else if (isSquareIcon) {
    logoClassName = "square-company-logo-image"
  }
  return (
    <div className={isFirst ? "first-company-section " + companySection : companySection}>
      <div className={vertical + "company-logo-container"}>
        <img className={logoClassName} src={logo}
             alt={companyName + " logo"}/>
      </div>
      <div className="work-text">
        <div className="work-heading">{companyName}</div>
        <div className="role-text">{roleText}
        </div>
        <p className="poppins-normal-black-14px work-normal-text">
          {descriptionText}
        </p>
      </div>
    </div>
  )
}

const Work = React.forwardRef<HTMLInputElement, ContentProps>(({flexColClassName}, ref) => {
  const contentWidthBreakpoint = 620;
  const viewportSize = useViewportSize();
  const mobile = isMobile(viewportSize);
  const contentWidth = mobile ? viewportSize.width : viewportSize.width * .685417;
  const isVerticalLayout = contentWidth < contentWidthBreakpoint;
  return (
    <div className={flexColClassName} id="work-content" ref={ref}>
      <div className="poppins-normal-black-24px">Work</div>
      <CompanySection
        logo={stripeFull}
        companyName={"Stripe"}
        roleText={
          <>
            Software Engineer / January 2022 - present<br/>
            Software Engineer Intern / Summer, Fall 2020
          </>
        }
        descriptionText={
          <>
            I worked as an intern at Stripe in Summer 2020, designing and implementing a new domain specific language
            for creating data flow graphs. The language explicitly defines a concurrent executable dependency graph
            between tasks and data, and compiles to Java. I created an IntelliJ IDEA plugin for the language featuring
            error highlighting, syntax highlighting, and autocompletion.
            <br/>
            <br/>
            I began working full-time at Stripe in January 2021, and have now primarily been focused on realtime
            orchestration.
          </>
        }
        isFirst={true}
        isSquareIcon={false}
        isVerticalLayout={isVerticalLayout}
      />
      <CompanySection
        logo={cuairFull}
        companyName={"Cornell University Unmanned Air Systems"}
        roleText={<>Platform Subteam Lead / Oct 2018 - Jan 2021</>}
        descriptionText={
          <>
            CUAir is a student-run team that designs, builds, and operates a custom search and rescue unmanned aerial
            system.
            I lead the Platform subteam in Fall 2018, and was responsible for managing tasking for six other
            undergraduates on our full-stack ground server, wifi-system, gimbal software, and plane software. I
            designed
            and implemented a
            system on-board the plane that communicates with a camera, gimbal, embedded
            device, and ground server to enable a two-pass system of target acquisition then tracking.
          </>
        }
        isFirst={false}
        isVerticalLayout={isVerticalLayout}
        isSquareIcon={false}/>
      <CompanySection
        logo={ngLogoFull}
        companyName={"Northrop Grumman"}
        roleText={<>Software Engineer Intern / Summer 2019</>}
        descriptionText={
          <>
            I interned at Northrop Grumman during Summer 2019, researching and implementing a machine
            learning
            algorithm for multivariate anomaly detection using a Generative Adversarial Networks (GAN). I also worked
            on
            multivariate changepoint detection using autoencoders. The algorithms were deployed using Docker and Kafka
            for data
            enrichment in a large-scale streaming analytics pipeline.
          </>
        }
        isFirst={false}
        isVerticalLayout={isVerticalLayout}
        isSquareIcon={false}/>
      <CompanySection
        logo={petr4Logo}
        companyName={"Cornell Netlab"}
        roleText={<>Undergraduate Researcher / Sep 2019 - May 2020</>}
        descriptionText={
          <>
            During sophomore year, I was a member of Cornell NetLab and worked on the Petr4
            project.
            The Petr4 project focused on developing formal semantics for the P4 Language.
          </>
        }
        isFirst={false}
        isVerticalLayout={isVerticalLayout}
        isSquareIcon={true}/>
      <CompanySection
        logo={cornellLogo}
        companyName={"Cornell University"}
        roleText={<>Teaching Assistant / Aug 2019 - May 2020</>}
        descriptionText={
          <>
            In Fall 2019, I worked as a course consultant for CS 3110: Functional Programming and Data Structures. In
            Spring 2020,
            I worked as a TA for CS 3110, co-leading a discussion section of 30+ students.
          </>
        }
        isVerticalLayout={isVerticalLayout}
        isFirst={false} isSquareIcon={true}/>
    </div>
  )
})

export default Work;